'use client';

import React from 'react';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { useLanguage } from '@/context/language-context';
import { Bot, Send } from 'lucide-react';
import { ScrollArea } from '@/components/ui/scroll-area';
import { toast } from 'sonner';

export function AiAssistant() {
  const { t } = useLanguage();
  const [isOpen, setIsOpen] = React.useState(false);
  const [conversation, setConversation] = React.useState<{ role: 'user' | 'assistant', content: string }[]>([]);
  const [input, setInput] = React.useState('');
  const [isLoading, setIsLoading] = React.useState(false);

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (input.trim()) {
      const userMessage = input;
      setConversation(prev => [...prev, { role: 'user', content: userMessage }]);
      setInput('');
      setIsLoading(true);

      try {
        const response = await fetch('/api/ai/chat', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ prompt: userMessage }),
        });

        if (!response.ok) {
          throw new Error('Failed to get response from AI');
        }

        const data = await response.json();
        setConversation(prev => [...prev, { role: 'assistant', content: data.response }]);
      } catch (error) {
        toast.error(t('toast.error.defaultAi'));
      } finally {
        setIsLoading(false);
      }
    }
  };

  return (
    <>
      <Button
        className="fixed bottom-4 right-4 z-50 rounded-full w-14 h-14 shadow-lg"
        onClick={() => setIsOpen(true)}
        aria-label={t('aiAssistant.button')}
      >
        <Bot className="w-6 h-6" />
      </Button>
      <Dialog open={isOpen} onOpenChange={setIsOpen}>
        <DialogContent className="sm:max-w-[425px] flex flex-col h-[60vh]">
          <DialogHeader>
            <DialogTitle>{t('aiAssistant.title')}</DialogTitle>
          </DialogHeader>
          <ScrollArea className="flex-grow pr-4">
            <div className="space-y-4">
              {conversation.map((entry, index) => (
                <div key={index} className={`flex ${entry.role === 'user' ? 'justify-end' : 'justify-start'}`}>
                  <div className={`px-3 py-2 rounded-lg ${entry.role === 'user' ? 'bg-primary text-primary-foreground' : 'bg-muted'}`}>
                    {entry.content}
                  </div>
                </div>
              ))}
              {isLoading && (
                <div className="flex justify-start">
                  <div className="px-3 py-2 rounded-lg bg-muted animate-pulse">
                    {t('ai.explainer.loading')}
                  </div>
                </div>
              )}
            </div>
          </ScrollArea>
          <DialogFooter>
            <form onSubmit={handleSubmit} className="flex w-full items-center space-x-2">
              <Input
                value={input}
                onChange={(e) => setInput(e.target.value)}
                placeholder={t('aiAssistant.placeholder')}
                disabled={isLoading}
              />
              <Button type="submit" disabled={isLoading} aria-label={t('common.send')}>
                <Send className="h-4 w-4" />
              </Button>
            </form>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </>
  );
}